<template>
  <div class="home">
    <div class="flod" @click="flod"/>
    <div class="home-head" v-show="isFlod">
      <el-button
        v-for="(item, index) in $router.options.routes[1].children"
        :key="index"
        @click="goto(item)"
      >
        {{ item.name }}
      </el-button>
    </div>
    <div class="scroll-top" @click="goTop()">顶</div>
    <div class="home-container" style="min-height: 100%;">
      <router-view />
    </div>
    <div class="footer-h">
      <p class="foot_p footer-p">
        Designed &amp; Powerd by <a href="/">jianghu</a>
        <br>
        Copyright© 2019-2023 奔跑的蜗牛
        <br>
        <a href="https://beian.miit.gov.cn/" target="_blank" rel="nofollow">粤ICP备2023033160号</a>
      </p>
      <div style="width:300px;margin:0 auto; padding:10px 0;text-align: center;">
        <a
          target="_blank"
          href="http://www.beian.gov.cn/portal/registerSystemInfo?recordcode=44030502009943"
          style="display:inline-block;text-decoration:none;height:20px;line-height:20px;"
        >
          <img :src="security" style="float:left;"/>
          <p style="float:left;height:20px;line-height:20px;margin: 0px 0px 0px 5px; color:#333;font-size: 12px;">粤公网安备 44030502009943号</p>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
  import security from '/static/images/security.png'
  export default {
    data() {
      return {
        security,
        isFlod: true,
      }
    },
    methods: {
      flod() {
        this.isFlod = !this.isFlod
      },
      goto(data) {
        console.log(data, 123)
        this.$router.push(`/${data.name}`)
      },
      goTop() {
        const dom = document.querySelector('html')
        let h = dom.scrollTop
        const subH = parseInt(h / 50)
        const timer = setInterval(function () {
          h -= subH
          if (h <= 0) {
            dom.scrollTop = 0
            clearInterval(timer)
          } else {
            dom.scrollTop = h
          }
        }, 1)
      },
    },
  }
</script>
